body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.LabyrinthOptionRow {
    width: 100%;
    display: block;
}
.MazeVisualization {
    border: black solid 1px;
    position: relative;
}

.MazeCell {
    border: black solid 1px;
    position: absolute;
}

.MazeCell.W {
    border-left: none;
}

.MazeCell.N {
    border-top: none;
}

.MazeCell.E {
    border-right: none;
}

.MazeCell.S {
    border-bottom: none;
}

.MazeCell.Pony {
    background-color: pink;
}

.MazeCell.Domokun {
    background-color: gray;
}

.MazeCell.End {
    background-color: green;
}

.MazeCell.Solution {
    background-color: yellow;
}
.ControlContainer {
    width: 50%;
    padding-top: 5px;
}

.ControlButton {
    height: 25px;
    border-radius: 5px;
    margin: 2px;
    border: none;
    background-color: #aabfb7;
}

.ControlButton[disabled] {
    background-color: #95a09a;
}

.ControlButton.toggleSolutionButton {
    float: left;
}

.ControlButton.executeSolutionButton {
    float: right;
}

.ControlButton.executeOneMoveButton {
    float: right;
}

/*Man is this a bad way to do the UI, ideally replace with some proper reactive handling. This works as a proof of concept, though*/
.ControlButton.northButton {
    position: relative;
    left: 101px;
    width: 25px;
}

.ControlButton.eastButton {
    position: relative;
    left: 70px;
    top: 26px;
    width: 25px;
}

.ControlButton.southButton {
    position: relative;
    top: 52px;
    left: 72px;
    width: 25px;
}

.ControlButton.westButton {
    position: relative;
    left: -12px;
    top: 26px;
    width: 25px;
}

.ControlButton.stayButton {
    position: relative;
    left: -15px;
    top: 26px;
    width: 25px;
}
